.mini-loader{
    display: inline-block;
    position: relative;
    width: 22px;
    height: 22px;
}

.mini-loader div{
    position: absolute;
    width: 11px;
    height: 11px;
    border-radius: 50%;
    background: #000;
    animation: mini-loader 1.2s linear infinite;
}

.mini-loader div:nth-child(1){ 
    top: 6px;
    left: 6px;
    animation-delay: 0s;
}

.mini-loader div:nth-child(2){
    top: 6px;
    left: 26px;
    animation-delay: -0.4s;
}

.mini-loader div:nth-child(3){
    top: 6px;
    left: 45px;
    animation-delay: -0.8s;
}

.mini-loader div:nth-child(4){
    top: 26px;
    left: 6px;
    animation-delay: -0.4s;
}

.mini-loader div:nth-child(5){
    top: 26px;
    left: 26px;
    animation-delay: -0.8s;
}

.mini-loader div:nth-child(6){
    top: 26px;
    left: 45px;
    animation-delay: -1.2s;
}

.mini-loader div:nth-child(7){
    top: 45px;
    left: 6px;
    animation-delay: -0.8s;
}

.mini-loader div:nth-child(8){
    top: 45px;
    left: 26px;
    animation-delay: -1.2s;
}

.mini-loader div:nth-child(9){
    top: 45px;
    left: 45px;
    animation-delay: -1.6s;
}

@keyframes mini-loader {
    0%,
    100%{
        opacity: 1;
    }

    50%{
        opacity: 0.5;
    }
}